.btn.social > :first-child,
.btn-large.social > :first-child,
.col.social > :first-child {
  border-right: 1px solid rgba(0, 0, 0, 0.2); }

.btn.social,
.btn-large.social,
.col.social {
  padding: 0 2rem 0 0; }

.btn.social i,
.btn-large.social i,
.col.social i {
  padding: 0 1rem;
  margin-right: 1rem; }

.btn.social-icon,
.btn-large.social-icon,
.col.social-icon {
  padding: 0; }

.btn.social-icon i,
.btn-large.social-icon i,
.col.social-icon i {
  padding: 0 1rem;
  margin-right: 0; }

.btn-large.social-icon {
  padding: 0 1rem; }

.col.social {
  width: 100%;
  text-align: left; }

.col.social i.fa-fw {
  width: 3.2rem; }

